import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projImg1 from "../assets/img/m15.jpeg";
import projImg2 from "../assets/img/m14.jpeg";
import projImg3 from "../assets/img/m15.jpeg";
import projImg4 from "../assets/img/m14.jpeg";
import colorSharp2 from "../assets/img/color-sharp2.png";
import "animate.css";
import TrackVisibility from "react-on-screen";
import React from "react";

export const Mazdhaar = () => {
  const projects = [
    {
      title: "Epsode 1: Mazdhaar-1",
      description: "Suspence & Drama",
      imgUrl: projImg1,
      projUrl: "https://www.youtube.com/embed/lDjhG-6MOlA",
    },
    {
      title: "Episode 2: Mazdhaar-2",
      description: "Suspence & Drama",
      imgUrl: projImg2,
      projUrl: "https://www.youtube.com/embed/3XCldntkTrk",
    },
    {
      title: "Mazdhaar-1 Teaser",
      description: "Suspence & Drama",
      imgUrl: projImg3,
      projUrl: "https://www.youtube.com/embed/LuW1Ks61JB4",
    },
  ];

  const projects2 = [
    {
      title: "Mazdhaar-2 Teaser",
      description: "Suspence & Drama",
      imgUrl: projImg4,
      projUrl: "https://www.youtube.com/embed/WS8jnc1EFiA",
    },
  ];

  return (
    <section className="project" id="project">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) => (
                <div
                  className={
                    isVisible ? "animate__animated animate__fadeIn" : ""
                  }
                >
                  <h2>Mazdhaar</h2>
                  <p>
                    It is the story of a boy who belongs to middle-class family
                    but his ambitions are high and wants to become an actor but
                    his father wants him to make an engineer. So this story
                    deals with the conflict of their ideologies with full twist
                    and turns.<br/><br/>
                    Genre : Suspence and Drama<br/>
                    Language : Hindi<br/>
                    Episodes : 2<br/>
                    Duration : 28 mins
                  </p>
                  <Tab.Container id="projects-tabs" defaultActiveKey="first">
                    <Nav
                      variant="pills"
                      className="nav-pills mb-5 justify-content-center align-items-center"
                      id="pills-tab"
                    >
                      <Nav.Item>
                        <Nav.Link eventKey="first">Tab 1</Nav.Link>
                      </Nav.Item>
                      {/* <Nav.Item>
                        <Nav.Link eventKey="second">Tab 2</Nav.Link>
                      </Nav.Item> */}
                      <Nav.Item>
                        <Nav.Link eventKey="third">Tab 2</Nav.Link>
                      </Nav.Item>
                    </Nav>
                    <Tab.Content
                      id="slideInUp"
                      className={
                        isVisible ? "animate__animated animate__slideInUp" : ""
                      }
                    >
                      <Tab.Pane eventKey="first">
                        <Row>
                          {projects.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane>
                      {/* <Tab.Pane eventKey="second">
                        <Row>
                          {projects.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane> */}
                      <Tab.Pane eventKey="third">
                        <Row>
                          {projects2.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane>
                    </Tab.Content>
                  </Tab.Container>
                </div>
              )}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      <img className="background-image-right" src={colorSharp2}></img>
    </section>
  );
};
