import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projImg1 from "../assets/img/m7.jpeg";
import projImg2 from "../assets/img/m6.jpeg";
import projImg3 from "../assets/img/m5.jpeg";
import projImg4 from "../assets/img/m4.jpeg";
import projImg5 from "../assets/img/m3.jpeg";
import projImg6 from "../assets/img/m8.jpeg";
import colorSharp2 from "../assets/img/color-sharp2.png";
import "animate.css";
import TrackVisibility from "react-on-screen";
import React from "react";

export const TheGray = () => {
  const projects = [
    {
      title: "Epsode 1",
      description: "The Plot Begin",
      imgUrl: projImg1,
      projUrl: "https://www.youtube.com/embed/MC2BF6f0eag",
    },
    {
      title: "Episode 2",
      description: "Accident OR Murder",
      imgUrl: projImg2,
      projUrl: "https://www.youtube.com/embed/YZAJBIMLKKA",
    },
    {
      title: "Episode 3",
      description: "Fortune OR Consiracy",
      imgUrl: projImg3,
      projUrl: "https://www.youtube.com/embed/PO7-Dxre-QU",
    },
  ];

  const projects2 = [
    {
      title: "Episode 4",
      description: "Towards Darkness",
      imgUrl: projImg4,
      projUrl: "https://www.youtube.com/embed/pTe1B8nwGd0",
    },
    {
      title: "Episode 5",
      description: "Dark Within",
      imgUrl: projImg5,
      projUrl: "https://www.youtube.com/embed/JhIwLzjjJA8",
    },
    {
      title: "Teaser",
      description: "Political Crime Thriller",
      imgUrl: projImg6,
      projUrl: "https://www.youtube.com/embed/qWfjWQ6scTE",
    },
  ];

  return (
    <section className="project" id="project">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) => (
                <div
                  className={
                    isVisible ? "animate__animated animate__fadeIn" : ""
                  }
                >
                  <h2>The Gray</h2>
                  <p>
                    It is story of a town and rivelary between two local politicians
                    but unfortunately one politician dies in road accident, so everyone 
                    suspects that another politician is the killer. So Let's help the Police to 
                    find who is the real culprit or it's actually an normal Accident.
                    <br />
                    <br />
                    Genre : Political Crime Thriller Drama
                    <br />
                    Language : Hindi
                    <br />
                    Episodes : 5
                    <br />
                    Duration : 67 mins
                  </p>
                  <Tab.Container id="projects-tabs" defaultActiveKey="first">
                    <Nav
                      variant="pills"
                      className="nav-pills mb-5 justify-content-center align-items-center"
                      id="pills-tab"
                    >
                      <Nav.Item>
                        <Nav.Link eventKey="first">Tab 1</Nav.Link>
                      </Nav.Item>
                      {/* <Nav.Item>
                        <Nav.Link eventKey="second">Tab 2</Nav.Link>
                      </Nav.Item> */}
                      <Nav.Item>
                        <Nav.Link eventKey="third">Tab 2</Nav.Link>
                      </Nav.Item>
                    </Nav>
                    <Tab.Content
                      id="slideInUp"
                      className={
                        isVisible ? "animate__animated animate__slideInUp" : ""
                      }
                    >
                      <Tab.Pane eventKey="first">
                        <Row>
                          {projects.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane>
                      {/* <Tab.Pane eventKey="second">
                        <Row>
                          {projects.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane> */}
                      <Tab.Pane eventKey="third">
                        <Row>
                          {projects2.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane>
                    </Tab.Content>
                  </Tab.Container>
                </div>
              )}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      <img className="background-image-right" src={colorSharp2}></img>
    </section>
  );
};
