import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projImg1 from "../assets/img/m15.jpeg";
import projImg2 from "../assets/img/m14.jpeg";
import projImg3 from "../assets/img/m13.jpeg";
import projImg4 from "../assets/img/m8.jpeg";
import projImg5 from "../assets/img/m1.jpeg";
import colorSharp2 from "../assets/img/color-sharp2.png";
import "animate.css";
import TrackVisibility from "react-on-screen";
import React from "react";

export const Projects = () => {
  const projects = [
    {
      title: "Mazdhaar-1",
      description: "Suspence & Drama",
      imgUrl: projImg1,
      projUrl:"https://www.youtube.com/embed/LuW1Ks61JB4"
    },
    {
      title: "Mazdhaar-2",
      description: "Suspence & Drama",
      imgUrl: projImg2,
      projUrl: "https://www.youtube.com/embed/WS8jnc1EFiA",
    },
    {
      title: "Antaryudh",
      description: "Mystery Crime Thriller",
      imgUrl: projImg3,
      projUrl: "https://www.youtube.com/embed/FnWhuMS2MUk",
    },
  ];

  const projects2 = [
    {
      title: "The Gray",
      description: "Political Crime Thriller",
      imgUrl: projImg4,
      projUrl: "https://www.youtube.com/embed/qWfjWQ6scTE",
    },
    {
      title: "Hakkekat",
      description: "Suspence",
      imgUrl: projImg5,
      projUrl: "https://www.youtube.com/embed/nEURWBMnyOI",
    },
  ];

  return (
    <section className="project" id="project">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) => (
                <div
                  className={
                    isVisible ? "animate__animated animate__fadeIn" : ""
                  }
                >
                  <h2>Short-Films/Series-Trailer</h2>
                  <p>
                    Here are the trailers of my short films and web series.
                    <br /> For watching Episodes of these films Scroll Down..
                  </p>
                  <Tab.Container id="projects-tabs" defaultActiveKey="first">
                    <Nav
                      variant="pills"
                      className="nav-pills mb-5 justify-content-center align-items-center"
                      id="pills-tab"
                    >
                      <Nav.Item>
                        <Nav.Link eventKey="first">Tab 1</Nav.Link>
                      </Nav.Item>
                      {/* <Nav.Item>
                        <Nav.Link eventKey="second">Tab 2</Nav.Link>
                      </Nav.Item> */}
                      <Nav.Item>
                        <Nav.Link eventKey="third">Tab 2</Nav.Link>
                      </Nav.Item>
                    </Nav>
                    <Tab.Content
                      id="slideInUp"
                      className={
                        isVisible ? "animate__animated animate__slideInUp" : ""
                      }
                    >
                      <Tab.Pane eventKey="first">
                        <Row>
                          {projects.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane>
                      {/* <Tab.Pane eventKey="second">
                        <Row>
                          {projects.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane> */}
                      <Tab.Pane eventKey="third">
                        <Row>
                          {projects2.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane>
                    </Tab.Content>
                  </Tab.Container>
                </div>
              )}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      <img className="background-image-right" src={colorSharp2}></img>
    </section>
  );
};
