import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projImg1 from "../assets/img/m12.jpeg";
import projImg2 from "../assets/img/m11.jpeg";
import projImg3 from "../assets/img/m10.jpeg";
import projImg4 from "../assets/img/m9.jpeg";
import projImg5 from "../assets/img/m13.jpeg";
import colorSharp2 from "../assets/img/color-sharp2.png";
import "animate.css";
import TrackVisibility from "react-on-screen";
import React from "react";

export const Antaryudh = () => {
  const projects = [
    {
      title: "Epsode 1",
      description: "Mystery",
      imgUrl: projImg1,
      projUrl: "https://www.youtube.com/embed/Qz6KwxHj9xI",
    },
    {
      title: "Episode 2",
      description: "Illusion",
      imgUrl: projImg2,
      projUrl: "https://www.youtube.com/embed/6mLT38dWfj8",
    },
    {
      title: "Episode 3",
      description: "Sinner",
      imgUrl: projImg3,
      projUrl: "https://www.youtube.com/embed/GF-VP4yN-kk",
    },
  ];

  const projects2 = [
    {
      title: "Episode 4",
      description: "Justice",
      imgUrl: projImg4,
      projUrl: "https://www.youtube.com/embed/GIPbkS6Nlz0",
    },
    {
      title: "Teaser",
      description: "Mystery Crime Thriller",
      imgUrl: projImg5,
      projUrl: "https://www.youtube.com/embed/FnWhuMS2MUk",
    },
  ];

  return (
    <section className="project" id="project">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) => (
                <div
                  className={
                    isVisible ? "animate__animated animate__fadeIn" : ""
                  }
                >
                  <h2>Antaryudh</h2>
                  <p>
                    It is the story of a town where mysteriously people are dying, police suspects that it is a kind
                    of serial killing. So two big officers came to investigate this, let's watch to know
                    whether they found that guy or they themselves got stuck in this mystery.
                    <br />
                    <br />
                    Genre : Suspence Crime Thriller
                    <br />
                    Language : Hindi
                    <br />
                    Episodes : 4
                    <br/>
                    Duration : 48 mins
                  </p>
                  <Tab.Container id="projects-tabs" defaultActiveKey="first">
                    <Nav
                      variant="pills"
                      className="nav-pills mb-5 justify-content-center align-items-center"
                      id="pills-tab"
                    >
                      <Nav.Item>
                        <Nav.Link eventKey="first">Tab 1</Nav.Link>
                      </Nav.Item>
                      {/* <Nav.Item>
                        <Nav.Link eventKey="second">Tab 2</Nav.Link>
                      </Nav.Item> */}
                      <Nav.Item>
                        <Nav.Link eventKey="third">Tab 2</Nav.Link>
                      </Nav.Item>
                    </Nav>
                    <Tab.Content
                      id="slideInUp"
                      className={
                        isVisible ? "animate__animated animate__slideInUp" : ""
                      }
                    >
                      <Tab.Pane eventKey="first">
                        <Row>
                          {projects.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane>
                      {/* <Tab.Pane eventKey="second">
                        <Row>
                          {projects.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane> */}
                      <Tab.Pane eventKey="third">
                        <Row>
                          {projects2.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane>
                    </Tab.Content>
                  </Tab.Container>
                </div>
              )}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      <img className="background-image-right" src={colorSharp2}></img>
    </section>
  );
};
