import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projImg1 from "../assets/img/m1.jpeg";
import projImg2 from "../assets/img/m2.jpeg";
import colorSharp2 from "../assets/img/color-sharp2.png";
import "animate.css";
import TrackVisibility from "react-on-screen";
import React from "react";

export const Hakkekat = () => {
  const projects = [
    {
      title: "Real within Reel",
      description: "Suspence Short Film",
      imgUrl: projImg1,
      projUrl: "https://www.youtube.com/embed/FotskTn7LJg",
    },
    {
      title: "Teaser",
      description: "Suspence Short Film",
      imgUrl: projImg2,
      projUrl: "https://www.youtube.com/embed/nEURWBMnyOI",
    },
  ];

  //   const projects2 = [
  //     {
  //       title: "Mazdhaar-2 Teaser",
  //       description: "Suspence & Drama",
  //       imgUrl: projImg4,
  //       projUrl: "https://www.youtube.com/embed/WS8jnc1EFiA"
  //     },
  //   ];

  return (
    <section className="project" id="project">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) => (
                <div
                  className={
                    isVisible ? "animate__animated animate__fadeIn" : ""
                  }
                >
                  <h2>Hakkekat</h2>
                  <p>
                    It is a story of three struggling actors who wants to become
                    bollywood stars and for that purpose they meet with a
                    producer to their own written storybut producer comes with a
                    condition. So Let's watch this short film to know how they
                    deal with that condition and whether their struggle ends or
                    they get into more struggle with lot of trouble.
                    <br />
                    <br />
                    Genre : Crime, Suspence and Drama
                    <br />
                    Language : Hindi
                    <br />
                    Episode : 1
                    <br />
                    Duration : 17 mins
                  </p>
                  <Tab.Container id="projects-tabs" defaultActiveKey="first">
                    <Tab.Content
                      id="slideInUp"
                      className={
                        isVisible ? "animate__animated animate__slideInUp" : ""
                      }
                    >
                      <Tab.Pane eventKey="first">
                        <Row>
                          {projects.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane>
                      {/* <Tab.Pane eventKey="second">
                        <Row>
                          {projects.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane> */}
                      {/* <Tab.Pane eventKey="third">
                        <Row>
                          {projects2.map((project, index) => {
                            return <ProjectCard key={index} {...project} />;
                          })}
                        </Row>
                      </Tab.Pane> */}
                    </Tab.Content>
                  </Tab.Container>
                </div>
              )}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      <img className="background-image-right" src={colorSharp2}></img>
    </section>
  );
};
